
.app{
    //height:calc(100vh - calc(100vh - 100%));
    height:100vh;
    //height: -webkit-fill-available;
    //max-height: -webkit-fill-available;
    //max-height: 100vh;
  //  min-height: -webkit-fill-available;
  

    .sections{
        width:100%;
        height:calc(100vh - 70px);
        height: calc(-webkit-fill-available - 70px);
        //height:calc(100vh - calc(100vh - 100%) -70px);
        //min-height: (-webkit-fill-available)-0.97em;
        //height: calc(100vh - env(safe-area-inset-bottom) -70px);
        background-color: white;
        position:relative;
        top:70px;
        scroll-behavior: smooth;
        scroll-snap-type: y mandatory;
        scrollbar-width:none; // firefox Only
        &::-webkit-scrollbar{
            display:none;
        }
        > *{
            width:100vw;
            height: calc(100vh - 70px);
            height: calc(-webkit-fill-available - 70px);
           //height:calc(100vh - 0.97em);
            scroll-snap-align: start;
        }
    }    
}